
.modal__container {
    overflow-x: auto;
    position: relative;
    margin: auto;
    background-color: $color-lightest;
    font-family: $font-family;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: $color-darkest;
    max-height: 100vh;
    width: 280px;
    padding: 48px 20px 41px 20px;

@media screen and (min-width: 768px)  {
        display: flex;
        margin: auto;
        width: 704px;
        height: auto;
        padding: 40px 30px;
} 

@media screen and (min-width: 1024px) {
width: 814px;
height: auto;
padding-top: 74px;

}
}

.image {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    display: block;

    max-width: 254px;
    height: auto;
    border-radius: 5px;
    margin-bottom: 20px;

}

.film__information {
    text-align: start;

@media screen and (min-width: 768px) {
    margin-left: 32px;
    
    }
  @media screen and (min-width: 1024px) {
      margin-left: 68px;
      max-width: 375px;
  }
}

.film__title {
    font-size: 20px;
    line-height: 1.15;
    margin-bottom: 20px;
    text-transform: uppercase;

    @media screen and (min-width: 768px) {
        font-size: 30px;
        line-height: 1.17;
    }
}

.film__item {
    display: flex;
    margin-bottom: 20px;
    &:nth-child(-n + 3) {
     margin-bottom: 10px;
    }
}

.film__details {
    color: $color-middle;
    min-width: 108px;

@media screen and (min-width: 768px) {
        min-width: 140px;
    }

}

.film__details-title {
    text-transform: uppercase;
}

.film__info--uper {
    text-transform: uppercase;
    line-height: 1.17;
}

.film__rating--orange {
    padding-left: 10px;
    padding-right: 10px;
    color: $color-lightest;
    background-color: $color-accent-2;

    border-radius: 5px;
}

.film__rating--divider {
    margin-right: 3px;
}

.film__about__title {
    text-transform: uppercase;
   
    margin-bottom: 10px;
}

.film__about__text {
    font-size: 12px;
    line-height: 1.67;
    margin-bottom: 21px;
    color: $color-darkest;
    font-weight: 500;
}

.film__button__wrapper {
    display: flex;
}

.close__icon-trailer{
     padding: 0px  5px 0px 10px;
     color: $color-accent;
     display: inline-block;
     width: 100px;
     height: 40px;
     stroke-width: 0;
     stroke: currentColor;
     fill: currentColor;
      
   
}

.close-icon {
    display: inline-block;
    width: 30px;
    height: 30px;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}