footer {
    background-color: $color-light ;
}

.footer-text {
    max-width: 240px;

    padding-top: 28.71px;
    padding-bottom: 29.7px;
    color: $color-dark;

    font-size: 14px;
    line-height: 1.17;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: $bp-tablet) {
        padding-top: 30px;
        padding-bottom: 30px;
        max-width: 100%;

        font-size: 16px;
        line-height: 1.17;
    }
}

.footer__social-icon {
    width: 14px;
    height: 12.75px;
    margin-right: 10px;
    margin-left: 10px;
    margin-top: 12px;

    @media screen and (min-width: $bp-tablet) {
        height: 12.88px;
        margin-top: 0px;
    }
}

.footer-text__command {
    display: inline-block;
    text-decoration: underline;

    &:hover,
    &:focus {
        color: #ff6b08;
        transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
}